<template>
    <ul>
        <li
            v-for="(item, index) in musicitem"
            :key="index"
            @click.stop="$emit('play', item)"
            :class="{
                active: currentmusic && currentmusic.id == item.id,
                playing: currentmusic && currentmusic.id == item.id && currentstate,
            }"
        >
            <span
                class="order"
                v-if="item.song.i"
                :style="{
                    left: item.song.i < 10 ? '3px' : item.song.i < 100 ? '3px' : '-2px',
                }"
                >{{ item.song.i }}</span
            >

            <div class="lefts" :style="{ paddingLeft: item.song.i ? '30px' : '0px' }">
                <div class="top">
                    {{ item.name }}
                    <span v-if="item.song.alias.length > 0">
                        <b v-for="(item, index) in item.song.alias" :key="item + index">({{ item }})</b>
                    </span>
                </div>
                <div class="bottom">
                    <b v-if="item.song.privilege" :class="{ sq: item.song.privilege.maxbr > 320000 }"></b>

                    <div class="artist-album">
                        <span class="artist" v-for="(item, index) in item.song.artists" :key="item.name + index">{{ item.name }}</span>
                        <span class="album">{{ item.song.album.name }}</span>
                    </div>
                </div>
            </div>
            <div class="right">
                <div class="animation">
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
                <span class="pause"></span>
            </div>
        </li>
    </ul>
</template>

<script>
export default {
    data() {
        return {
            isaa: true,
        };
    },
    props: {
        musicitem: Array,
        currentstate: {
            type: Boolean,
            default: true,
        },
        currentmusic: {
            type: Object,
            default: () => {
                return null;
            },
        },
    },
};
</script>

<style lang="scss" scoped>
ul {
    li {
        line-height: 20px;
        border-bottom: 2px solid rgba(128, 128, 128, 0.1);
        margin-left: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        &:nth-of-type(-n + 3) {
            .order {
                color: red;
            }
        }
        .order {
            position: absolute;
            top: 15px;
            color: gray;
        }
        .lefts {
            font-size: 12px;
            width: 89%;
            padding-left: 30px;
            .top {
                font-size: 16px;
                margin: 6px 15px 0px 0px;
                box-sizing: border-box;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 1;
                overflow: hidden;
                span {
                    color: gray;
                }
            }
            .bottom {
                color: gray;
                margin: 2px 0px;
                display: flex;
                align-items: center;
                .sq {
                    background-image: url("../assets/index_icon.png");
                    background-repeat: no-repeat;
                    background-size: 249px 145px;
                    background-position: 0px 0px;
                    display: inline-block;
                    width: 18px;
                    height: 12px;
                    margin-right: 4px;
                }
                .artist-album {
                    width: 90%;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 1;
                    overflow: hidden;
                    .artist {
                        //   // width:50px;
                        &::before {
                            content: "/";
                            padding: 0px 4px;
                        }
                        &:first-of-type {
                            &::before {
                                content: "";
                                padding: 0px;
                            }
                        }
                    }
                    .album {
                        &::before {
                            content: "-";
                            padding: 0px 5px;
                        }
                        // width: 135px;
                        // display: -webkit-box;
                        // -webkit-box-orient: vertical;
                        // -webkit-line-clamp: 1;
                        // overflow: hidden;
                    }
                }
            }
        }
        .right {
            width: 38px;
            .pause {
                background-image: url("../assets/index_icon.png");
                background-repeat: no-repeat;
                background-size: 166px 97px;
                background-position: -24px 0;
                display: inline-block;
                width: 22px;
                height: 22px;
            }
        }
        &.active {
            .right {
                .pause {
                    display: none;
                }
                .animation {
                    width: 25px;
                    display: flex;
                    justify-content: center;
                    margin-top: 8px;
                    height: 20px;
                    span {
                        width: 2px;
                        height: 20px;
                        background-color: red;
                        margin-right: 3px;
                        margin-left: 0px;
                        animation: jumpjump 0.8s linear infinite;
                        transform-origin: bottom;
                        animation-play-state: paused;
                        &:first-of-type {
                            animation-delay: -0.4s;
                        }
                        &:nth-of-type(2) {
                            animation-delay: -0.2s;
                        }
                        &:nth-of-type(3) {
                            animation-delay: 0s;
                        }
                        &:nth-of-type(4) {
                            animation-delay: 0.2s;
                        }
                        &:nth-of-type(5) {
                            animation-delay: 0.4s;
                        }
                    }
                }
            }
        }
        &.playing {
            .right {
                .animation {
                    span {
                        animation-play-state: running;
                    }
                }
            }
        }
    }
}
@keyframes jumpjump {
    0% {
        transform: scaleY(0.5);
    }
    50% {
        transform: scaleY(1);
    }
    100% {
        transform: scaleY(0.5);
    }
}
</style>
