<template>
    <div class="music-home" :class="{ bottom: currentmusic.id ? true : false }">
        <title-bar>编辑推荐</title-bar>
        <music-list v-if="musiclist.length > 0" :musiclist="musiclist"></music-list>
        <load-animation v-else></load-animation>
        <title-bar>最新音乐</title-bar>
        <music-item v-if="musicitem.length > 0" :musicitem="musicitem" :currentmusic="currentmusic" :currentstate="currentstate" @play="$emit('play', $event)"></music-item>
        <load-animation v-else></load-animation>
    </div>
</template>

<script>
import TitleBar from "@/components/TitleBar.vue";
import MusicList from "@/components/MusicList.vue";
import MusicItem from "@/components/MusicItem.vue";
export default {
    props: ["currentmusic", "currentstate", ""],
    data() {
        return {
            musiclist: [], //存储编辑推荐的数据
            musicitem: [], //存储最新音乐的数据
        };
    },
    components: {
        TitleBar,
        MusicList,
        MusicItem,
    },
    created() {
        this.getMusicLst();
        this.getMusicItem();
    },
    methods: {
        //  异步获取编辑推荐的数据
        async getMusicLst() {
            await this.axios.get("/personalized").then((res) => {
                //  console.log(res.result);
                this.musiclist = res.result.slice(0, 6);
            });
        },
        //  异步获取最新音乐的数据
        async getMusicItem() {
            await this.axios.get("/personalized/newsong").then((res) => {
                console.log(res.result);
                this.musicitem = res.result;
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.music-home {
    &.bottom {
        padding-bottom: 56px;
    }
}
</style>
