<template>
    <div class="muisc-song-list">
        <div class="music-list">
            <svg
                t="1652749346406"
                class="icon_Retracting"
                @click.stop="$emit('fanghui', false)"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="2830"
                width="30"
                height="30"
            >
                <path
                    d="M139.648 376.704a32 32 0 0 1 37.44-51.744l3.264 2.336L512 600.768l331.648-273.472a32 32 0 0 1 42.304 1.408l2.752 2.944a32 32 0 0 1-1.408 42.304l-2.944 2.752-352 290.24a32 32 0 0 1-37.376 2.4l-3.328-2.4-352-290.24z"
                    p-id="2831"
                    fill="#111"
                ></path>
            </svg>
            <h3 class="t"><b>当前播放</b></h3>
            <div class="playlist_box">
                <ul class="playlist">
                    <li
                        :class="{
                            active: currentmusic && currentmusic.id == item.id,
                        }"
                        class="playlist_item"
                        v-for="(item, index) in musiclist"
                        :key="index"
                    >
                        <div class="left">
                            <div class="animation">
                                <span></span>
                                <span></span>
                                <span></span>
                                <span></span>
                                <span></span>
                            </div>
                            <span class="pause"></span>
                        </div>
                        <div class="playlist_item_item" @click.stop="$emit('play', item)">
                            <div class="item_name">
                                {{ item.name }} <span>{{ item.alia }}</span
                                >-<span class="songer" v-for="i in item.song.artists" :key="i.name">{{ i.name }}</span>
                            </div>
                        </div>
                        <span class="iconfont icon-cuowu" @click.stop="remove(item, musiclist)"></span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        musiclist: Array,
        currentmusic: {
            type: Object,
            default: () => {
                return null;
            },
        },
        currentstate: {
            type: Boolean,
            default: true,
        },
    },
    created() {},
    methods: {
        remove(str, arr) {
            for (var i = 0; i < arr.length; i++) {
                if (arr[i] == str) {
                    arr.splice(i, 1);
                    i--;
                }
            }
            return arr;
        },
    },
};
</script>

<style lang="scss" scoped>
.muisc-song-list {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.592);
    position: fixed;
    left: 0;
    bottom: 60px;
    // bottom: 0px;
    z-index: 50;
    .icon-fanhui {
        display: inline-block;
        font-size: 18px;
        color: black;
        margin-left: 16px;
    }
    .music-list {
        width: 96vw;
        height: 60vh;
        background-color: rgba(255, 255, 255, 1);
        border-radius: 23px;
        position: fixed;
        left: 2%;
        bottom: 10px;
        z-index: 52;
        padding: 6px;
        overflow-y: auto;
        .t {
            b {
                font-weight: bolder;
            }
        }
        .playlist_box {
            margin-top: 10px;
            width: 100%;
            height: 46vh;
            overflow: scroll;
            .playlist {
                .playlist_item {
                    display: flex;
                    height: 40px;
                    justify-content: space-between;
                    // line-height: 40px;
                    .icon-cuowu {
                        text-align: center;
                        width: 40px;
                        height: 40px;
                        line-height: 40px;
                    }
                    .playlist_item_item {
                        overflow: hidden;
                        width: 80%;
                        white-space: nowrap;
                        display: inline-block;
                        position: relative;
                        height: 40px;
                        line-height: 40px;
                        .item_name {
                            width: 100%;
                            display: inline-block;
                            .songer {
                                font-size: 12px;
                                color: #666;
                            }
                        }
                    }
                }
            }
        }
    }
}
.active {
    display: inline-block;
    background-color: rgba(0, 0, 0, 0.1);
    .left {
        width: 38px;
        height: 38px;
        .animation {
            width: 25px;
            display: flex;
            justify-content: center;
            margin-top: 8px;
            height: 20px;
            span {
                width: 2px;
                height: 20px;
                background-color: red;
                margin-right: 3px;
                margin-left: 0px;
                transform-origin: bottom;
                animation-play-state: paused;
                &:first-of-type {
                    animation: jumpjump 0.8s linear infinite;
                    animation-delay: -0.4s;
                }
                &:nth-of-type(2) {
                    animation: jumpjump 0.8s linear infinite;
                    animation-delay: -0.2s;
                }
                &:nth-of-type(3) {
                    animation: jumpjump 0.8s linear infinite;
                    animation-delay: 0s;
                }
                &:nth-of-type(4) {
                    animation: jumpjump 0.8s linear infinite;
                    animation-delay: 0.2s;
                }
                &:nth-of-type(5) {
                    animation: jumpjump 0.8s linear infinite;
                    animation-delay: 0.4s;
                }
            }
        }
    }
}
@keyframes jumpjump {
    0% {
        transform: scaleY(0.5);
    }
    50% {
        transform: scaleY(1);
    }
    100% {
        transform: scaleY(0.5);
    }
}
</style>
