<template>
    <div class="music-play">
        <div
            class="music-bar"
            v-if="currentmusic && showbar"
            @click.stop="showpage = true"
            :class="{
                playing: currentstate,
            }"
        >
            <div class="pic">
                <img :src="currentmusic.picUrl ? currentmusic.picUrl : currentmusic.al.picUrl" alt="" />
            </div>
            <div class="move" :class="{ playing: currentstate }">
                <div class="music-name">
                    <span class="name">
                        {{ currentmusic.name }}
                    </span>
                    <span v-if="currentmusic.song" class="alias">
                        <b v-for="(item, index) in currentmusic.song.alias" :key="index">({{ item }})</b>
                    </span>
                    <br />
                    <span class="artists" v-for="(i, index) in currentmusic.song.artists" :key="index">
                        {{ i.name }}
                    </span>
                </div>
            </div>
            <div class="anniu">
                <canvas id="mycanvas" ref="mycanvas" width="36" height="36"></canvas>
                <!-- 暂停currentstate为FALSE -->
                <div v-if="!currentstate" class="zant">
                    <span class="iconfont icon-24gf-play" @click.stop="$refs.audio.play()"></span>
                </div>
                <!-- 播放 currentstate为TRUE -->
                <div class="bof" v-else>
                    <span class="iconfont icon-zanting1" @click.stop="$refs.audio.pause()"></span>
                </div>
            </div>
            <svg t="1652703487027" class="icon_list" @click.stop="anas" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="52472" width="30" height="30">
                <path
                    d="M981.333333 917.333333a21.333333 21.333333 0 0 1-21.333333 21.333334H64a21.333333 21.333333 0 0 1 0-42.666667h896a21.333333 21.333333 0 0 1 21.333333 21.333333zM533.333333 170.666667h426.666667a21.333333 21.333333 0 0 0 0-42.666667H533.333333a21.333333 21.333333 0 0 0 0 42.666667z m426.666667 341.333333H533.333333a21.333333 21.333333 0 0 0 0 42.666667h426.666667a21.333333 21.333333 0 0 0 0-42.666667zM62.833333 593.12a37.213333 37.213333 0 0 0 38.713334-2.666667l309.333333-218.633333a37.333333 37.333333 0 0 0 0-60.973333l-309.333333-218.633334A37.333333 37.333333 0 0 0 42.666667 122.7V560a37.2 37.2 0 0 0 20.166666 33.12z"
                    fill="#333"
                    p-id="52473"
                ></path>
            </svg>
        </div>
        <!-- <transition name="float"> -->
        <show-list
            v-if="showlist"
            :musiclist="musiclist"
            :currentstate="currentstate"
            :currentmusic="currentmusic"
            @play="$emit('play', $event)"
            :showlist="showlist"
            @fanghui="showlist = $event"
        ></show-list>
        <!-- </transition> -->
        <music-page
            v-if="showpage"
            :currentmusic="currentmusic"
            :currentstate="currentstate"
            @bofang="$refs.audio.play()"
            @zanting="$refs.audio.pause()"
            @next-song="$emit('next-song')"
            @prev-song="$emit('prev-song')"
            :currentTime="currentTime"
            :duration="duration"
            :musicTime="musicTime"
            @update:currenTtime="updateCurrentTimeValue"
            :suiji="suiji"
            @update:suiji="$emit('update:suiji')"
            @update:shunx="$emit('update:shunx')"
        ></music-page>

        <audio
            autoplay
            controls
            ref="audio"
            :src="`https://music.163.com/song/media/outer/url?id=${currentmusic.id}.mp3`"
            @play="$parent.currentstate = true"
            @pause="$parent.currentstate = false"
            @timeupdate="timeupdate"
            @ended="$emit('next-song')"
        ></audio>
    </div>
</template>

<script>
// showlist = !showlist

import MusicPage from "./MusicPage.vue";
import ShowList from "./ShowList.vue";
export default {
    components: { MusicPage, ShowList },
    props: {
        currentmusic: {
            type: Object,
            default: () => {
                return null;
            },
        },
        currentstate: {
            type: Boolean,
            default: true,
        },
        showbar: {
            type: Boolean,
            default: true,
        },
        musiclist: Array,
        suiji: Boolean,
    },
    data() {
        return {
            showpage: false,
            showlist: false,
            currentTime: 0,
            duration: 0,
        };
    },
    methods: {
        // 点击列表
        anas() {
            // this.$emit("abc");
            this.showlist = !this.showlist;
        },
        timeupdate(event) {
            // console.log(event.target.currentTime,event.target.duration);
            this.currentTime = event.target.currentTime;
            this.duration = event.target.duration;
        },
        updateCurrentTimeValue(event) {
            this.$refs.audio.currentTime = event;
        },
    },
    computed: {
        musicTime() {
            //当前播放时间 / 总时长
            return this.duration === 0 ? 0 : this.currentTime / this.duration;
        },
    },
    watch: {
        musicTime: function (n) {
            const ctx = this.$refs.mycanvas.getContext("2d");
            ctx.clearRect(0, 0, 36, 36);
            ctx.beginPath();
            ctx.lineWidth = 1;
            ctx.arc(18, 18, 16, -Math.PI / 2, (Math.PI / 180) * 360 - Math.PI / 2);
            ctx.strokeStyle = "rgba(0,0,0,0.2)";
            ctx.stroke();

            ctx.beginPath();
            ctx.lineWidth = 1;
            ctx.arc(18, 18, 16, -Math.PI / 2, (Math.PI / 180) * 360 * n - Math.PI / 2);
            ctx.strokeStyle = "red";
            ctx.stroke();
            ctx.closePath();
        },
    },
};
</script>

<style lang="scss" scoped>
.icon_list {
    margin-right: 10px;
}
.music-play {
    position: fixed;
    width: 100%;
    height: 60px;
    bottom: 0px;
    left: 0;
    z-index: 45;
    .music-bar {
        width: 100%;
        height: 60px;
        background-color: white;
        border-radius: 20px 20px 0px 0px;
        position: fixed;
        bottom: 0;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: space-around;
        .pic {
            width: 46px;
            height: 46px;
            margin-left: 10px;
            border-radius: 50%;
            background-color: #333;
            animation: roll 2s linear infinite;
            animation-play-state: paused;
            img {
                margin: 3px 3px;
                width: 40px;
                display: block;
                border-radius: 50%;
            }
        }
        .move {
            overflow: hidden;
            .music-name {
                width: 200px;
                animation: move 6s linear infinite;
                animation-play-state: paused;
                .name {
                    font-size: 16px;
                    word-break: keep-all;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    line-height: 18px;
                    animation-delay: 5s;
                    color: #333;
                }
                .alias {
                    font-size: 15px;
                    word-break: keep-all;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                .artists {
                    font-size: 12px;
                    word-break: keep-all;
                    white-space: nowrap;
                    overflow: hidden;
                    text-align: center;
                    text-overflow: ellipsis;
                    animation-delay: 5s;
                }
            }
            &.playing {
                .music-name {
                    animation-play-state: running;
                }
            }
        }
        .anniu {
            position: relative;
            #mycanvas {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: -1;
            }
            .zant {
                width: 36px;
                height: 36px;
                .icon-24gf-play {
                    font-size: 14px;
                    line-height: 36px;
                    padding-left: 11px;
                    box-sizing: border-box;
                }
            }
            .bof {
                width: 36px;
                height: 36px;
                .icon-zanting1 {
                    font-size: 14px;
                    line-height: 36px;
                    padding-left: 11px;
                    box-sizing: border-box;
                }
            }
        }

        .icon-liebiao {
            font-size: 24px;
        }
        &.playing {
            .pic {
                animation-play-state: running;
            }
        }
    }
    audio {
        position: fixed;
        top: 60px;
        left: 0;
        display: none;
    }
}
@keyframes roll {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
@keyframes move {
    from {
        transform: translateX(100%);
    }
    to {
        transform: translateX(-100%);
    }
}
// .float-enter{
//   transform: translateY(100%);
// }
// .float-enter-to,.float-leave{
//   transform: translateY(0%);
// }
// .float-enter-active,.float-leave-active{
//   transition: all 2s linear;
// }
// .float-leave-to{
//    transform: translateY(100%);
// }
</style>
