            <svg
                t="1652749346406"
                class="icon_Retracting"
                @click.stop="$parent.showpage = false"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="2830"
