                    fill="#e6e6e6"
                ></path>
            </svg>
            <div class="move" :class="{ playing: currentstate }">
                <div class="title">
                    <div class="name">{{ currentmusic.name }}</div>
                    <div v-if="currentmusic.song" class="alias">
                        <b v-for="(item, index) in currentmusic.song.alias" :key="index">({{ item }})</b>
                    </div>
                    <div class="all-artists">
                        <div class="title-artists" v-for="i in currentmusic.song.artists" :key="i.id">&nbsp; {{ i.name }}</div>
                    </div>
                </div>
            </div>

            <span class="iconfont icon-fenxiang"></span>
        </div>

        <div class="music-page-middle" :class="{ playing: currentstate }" v-if="!showlyrc" @click.stop="showlyrc = true">
            <div class="pic-top">
                <img src="../assets/needle-ab.png" alt="" />
            </div>
            <div class="pic">
                <img :src="currentmusic.picUrl ? currentmusic.picUrl : currentmusic.al.picUrl" alt="" />
            </div>
        </div>
        <div class="lyric" v-else @click.stop="showlyrc = false">
            <ul :style="{ transform: `translateY(-${40 * currentIndex}px)` }">
                <li v-for="(i, index) in lyricArray" :key="index" :class="{ active: currentIndex == index }">{{ i.lyric }}</li>
            </ul>
        </div>

