</template>

<script>
import ShowList from "./ShowList.vue";
export default {
    components: { ShowList },
    props: {
        currentmusic: {
            type: Object,
            default: () => {
                return null;
            },
        },
        currentstate: {
            type: Boolean,
            default: true,
        },
        showlist: Boolean,
        currentTime: Number,
        duration: Number,
        musicTime: Number,
        suiji: Boolean,
    },
    data() {
        return {
            showlyrc: false,
            lyric: null,
            value: this.currentTime,
            inputing: false,
        };
    },
    created() {
        this.getLyric();
    },
    methods: {
        async getLyric() {
            await this.axios.get("/lyric?id=" + this.currentmusic.id).then((res) => {
                // console.log(res.lrc.lyric);
                this.lyric = res.lrc.lyric;
            });
        },
        changing() {
            this.inputing = false;
            this.$emit("update:currenTtime", this.value);
        },
    },
    computed: {
        updateCurrentTime() {
            let time = Math.ceil(this.currentTime);
            const min = parseInt(time / 60) < 10 ? "0" + parseInt(time / 60) : parseInt(time / 60);
            const seconde = parseInt(time % 60) < 10 ? "0" + parseInt(time % 60) : parseInt(time % 60);
            return min + ":" + seconde;
        },
        updateDuration() {
            let time = Math.ceil(this.duration);
            const min = parseInt(time / 60) < 10 ? "0" + parseInt(time / 60) : parseInt(time / 60);
            const seconde = parseInt(time % 60) < 10 ? "0" + parseInt(time % 60) : parseInt(time % 60);
            return min + ":" + seconde;
        },
        lyricArray() {
            if (!this.lyric) return [];
            let lyricArr = this.lyric.split("\n");
            lyricArr.pop(); //删除最后一条空歌词
            //  [00:00.000] 作词 : 唐思淼
            let lyricReg = /\[(\d+):(\d+\.\d+)\]([\s\S.]*)/;
            return lyricArr.map((value) => {
                if (lyricReg.test(value)) {
                    let ly = RegExp.$3.length < 1 ? "......" : RegExp.$3;
                    return {
                        time: parseInt(RegExp.$1 * 60) + parseFloat(RegExp.$2),
                        lyric: ly,
                    };
                }
            });
        },
        currentIndex() {
            let index = 0;
            for (let i = 0; i < this.lyricArray.length - 1; i++) {
                let Itime = this.lyricArray[i].time;
                let nexttime = this.lyricArray[i + 1].time;
                if (this.currentTime >= Itime && this.currentTime <= nexttime) {
                    index = i;
                    return index;
                }
            }
            return index;
        },
    },
    watch: {
        currentTime: function (n) {
            if (!this.inputing) {
                this.value = n;
            }
        },
        "currentmusic.id": function () {
            console.log("currentmusic.id=>", this.currentmusic.id);
            this.getLyric();
        },
    },
};
</script>

