<template>
    <div id="app">
        <tab-nav></tab-nav>
        <router-view @play="play" :currentstate="currentstate" :currentmusic="currentmusic" />
        <music-play
            v-if="showbar"
            :currentstate="currentstate"
            :currentmusic="currentmusic"
            :showbar="showbar"
            :musiclist="musiclist"
            @play="play"
            @next-song="nextSong"
            @prev-song="prevSong"
            @abc="anas"
            :suiji="suiji"
            @update:suiji="updateSuiji"
            @update:shunx="updateShunx"
        ></music-play>
    </div>
</template>
<script>
import TabNav from "@/components/TabNav.vue";
import MusicPlay from "@/components/MusicPlay.vue";
export default {
    components: {
        TabNav,
        MusicPlay,
    },
    data() {
        return {
            currentmusic: {},
            currentstate: false,
            showbar: false,
            musiclist: [],
            suiji: false,
        };
    },
    methods: {
        anas() {
            this.musiclist = this.musiclist.map((item) => {
                if (item.song.i) {
                    delete item.song.i;
                }
                return item;
            });
            console.log("123", this.musiclist);
        },
        play(event) {
            console.log("状态=>", this.currentstate);
            this.currentmusic = event;
            this.musiclist.push(this.currentmusic);
            this.musiclist = [...new Set(this.musiclist)];
            console.log("歌单列表=>", this.musiclist);
            console.log("点击歌单=>", this.currentmusic);
            this.currentstate = true;
            this.showbar = true;
            console.log(event);
            console.log("点击过后状态=>", this.currentstate);
        },
        nextSong() {
            var currentindex = this.musiclist.findIndex((o) => {
                return o.id === this.currentmusic.id;
            });
            console.log(currentindex);
            if (this.suiji == false) {
                var index = currentindex === this.musiclist.length - 1 ? 0 : currentindex + 1;
                console.log(index);
                this.currentmusic = this.musiclist[index];
            } else {
                var indexall = Math.floor(Math.random() * this.musiclist.length);
                console.log(indexall);
                this.currentmusic = this.musiclist[indexall];
            }
        },
        prevSong() {
            var currentindex = this.musiclist.findIndex((o) => {
                return o.id === this.currentmusic.id;
            });
            if (this.suiji == false) {
                var index = currentindex === 0 ? this.musiclist.length - 1 : currentindex - 1;
                console.log(index);
                this.currentmusic = this.musiclist[index];
            } else {
                var indexall = Math.floor(Math.random() * this.musiclist.length);
                console.log(indexall);
                this.currentmusic = this.musiclist[indexall];
            }
        },
        updateSuiji() {
            this.suiji = true;
        },
        updateShunx() {
            this.suiji = false;
        },
    },
};
</script>
<style lang="scss" scoped></style>
